"use client";
import { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import dayjs from "dayjs";

// ✅ Dynamically import Plotly to avoid SSR issues
const Plot = dynamic(() => import("react-plotly.js"), { ssr: false });

interface SP500StockData {
    Date: string;
    Open: number;
    High: number;
    Low: number;
    Close: number;
    Volume: number;
    Ticker: string;
    Year: number;
    Month: number;
    Day: number;
    moving_avg_20: number;
    volatility_20: number;
    moving_avg_50: number;
    volatility_50: number;
    Change: number;
}

interface PerformanceChartProps {
    data: SP500StockData[];
}

export default function PerformanceChart({ data }: PerformanceChartProps) {
    // ✅ Extract unique tickers from the dataset
    const availableTickers = Array.from(new Set(data.map(stock => stock.Ticker))).sort();

    // ✅ Set the default ticker (first available or "AAPL")
    const [selectedTicker, setSelectedTicker] = useState(availableTickers[0] || "AAPL");

    useEffect(() => {
        console.log("Selected Ticker:", selectedTicker);
    }, [selectedTicker]);

    if (data.length === 0) {
        return <div className="p-4 border rounded-lg shadow-lg bg-white">No data available</div>;
    }

    // ✅ Filter data for the selected ticker (last 12 months)
    const filteredData = data
        .filter(stock => stock.Ticker === selectedTicker && stock.Year >= 2023) // Only show 2023-2024
        .map(stock => ({
            ...stock,
            Date: dayjs(stock.Date).format("YYYY-MM-DD"), // ✅ Format Date correctly
        }));

    if (filteredData.length === 0) {
        return <div className="p-4 border rounded-lg shadow-lg bg-black">No data available for {selectedTicker}</div>;
    }

    return (
        <div className="p-4 border rounded-lg shadow-lg bg-black">
            <h2 className="text-xl font-bold mb-4 text-white">Stock Performance: {selectedTicker}</h2>

            {/* ✅ Dropdown for selecting stock ticker */}
            <select
                className="mb-4 p-2 border rounded-lg"
                value={selectedTicker}
                onChange={(e) => setSelectedTicker(e.target.value)}
            >
                {availableTickers.map(ticker => (
                    <option key={ticker} value={ticker}>
                        {ticker}
                    </option>
                ))}
            </select>

            <Plot
                data={[
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.Close),
                        type: "scatter",
                        mode: "lines",
                        name: "Close Price",
                        line: { color: "#8884d8", width: 2 },
                    },
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_20),
                        type: "scatter",
                        mode: "lines",
                        name: "20-Day Moving Avg",
                        line: { color: "#82ca9d", width: 2, dash: "dash" },
                    },
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_50),
                        type: "scatter",
                        mode: "lines",
                        name: "50-Day Moving Avg",
                        line: { color: "#ff7300", width: 2, dash: "dot" },
                    },
                ]}
                layout={{
                    autosize: true,
                    plot_bgcolor: "#121212",  // Dark background
                    paper_bgcolor: "#121212", // Dark container background
                    font: { color: "#ffffff" }, // White text
                    xaxis: {
                        title: "Date",
                        tickformat: "%Y-%m-%d",
                        showgrid: true,
                        gridcolor: "#333", // Subtle grid lines
                    },
                    yaxis: {
                        title: "Stock Price",
                        showgrid: true,
                        gridcolor: "#333",
                    },
                    margin: { t: 10, l: 50, r: 10, b: 50 },
                    legend: {
                        bgcolor: "#1e1e1e", // Dark legend box
                        font: { color: "#ffffff" },
                    },
                }}
                    useResizeHandler
                style={{ width: "100%", height: "400px" }}
            />
        </div>
    );
}

